import React, { useEffect, useState } from 'react';
import axios from 'axios';
import './GenresPage.css';

const API_BASEURL = 'https://api.themoviedb.org/3';
const API_KEY = 'a04268d7932b9b6360e6b4d42fd27509';

const GenresPage = () => {
    const [genres, setGenres] = useState([]);
    const [selectedGenre, setSelectedGenre] = useState('');
    const [movies, setMovies] = useState([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchGenres = async () => {
            try {
                const response = await axios.get(`${API_BASEURL}/genre/movie/list`, {
                    params: {
                        api_key: API_KEY
                    }
                });
                setGenres(response.data.genres);
                setLoading(false);
            } catch (error) {
                console.error('Error fetching genres:', error);
                setLoading(false);
            }
        };

        fetchGenres();
    }, []);

    useEffect(() => {
        if (selectedGenre) {
            const fetchMovies = async () => {
                try {
                    const response = await axios.get(`${API_BASEURL}/discover/movie`, {
                        params: {
                            api_key: API_KEY,
                            with_genres: selectedGenre
                        }
                    });
                    setMovies(response.data.results);
                } catch (error) {
                    console.error('Error fetching movies:', error);
                }
            };

            fetchMovies();
        }
    }, [selectedGenre]);

    if (loading) {
        return <div>Loading...</div>;
    }

    return (
        <div className="container">
            <h1>Genres Page</h1>
            <select
                value={selectedGenre}
                onChange={e => setSelectedGenre(e.target.value)}
                className="genres-dropdown"
            >
                <option value="">Select a genre</option>
                {genres.map(genre => (
                    <option key={genre.id} value={genre.id}>
                        {genre.name}
                    </option>
                ))}
            </select>
            <div className="movies-grid">
                {movies.map(movie => (
                    <div key={movie.id} className="movie">
                        <img
                            src={`https://image.tmdb.org/t/p/w500${movie.poster_path}`}
                            alt={movie.title}
                            className="movie-poster"
                        />
                        <div className="movie-details">
                            <h2>{movie.title}</h2>
                            <p className="movie-overview">{movie.overview}</p>
                        </div>
                    </div>
                ))}
            </div>
        </div>
    );
};

export default GenresPage;
