import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useParams } from 'react-router-dom';
import './SingleMoviePage.css'; // Import the CSS file

const API_BASEURL = 'https://api.themoviedb.org/3';
const API_KEY = 'a04268d7932b9b6360e6b4d42fd27509';
const IMAGE_BASEURL = 'https://image.tmdb.org/t/p/w500';

const SingleMoviePage = () => {
    const { movieId } = useParams();
    const [movieDetails, setMovieDetails] = useState(null);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        const fetchMovieDetails = async () => {
            try {
                const response = await axios.get(`${API_BASEURL}/movie/${movieId}?api_key=${API_KEY}&language=en-US`);
                setMovieDetails(response.data);
                setLoading(false);
            } catch (error) {
                console.error('Error fetching movie details:', error);
                setLoading(false);
            }
        };

        fetchMovieDetails();
    }, [movieId]);

    if (loading) {
        return <div className="loading-container"><div className="loading-text">Loading...</div></div>;
    }

    if (!movieDetails) {
        return <div className="error-text">Movie details not found.</div>;
    }

    return (
        <div className="single-movie-container">
            <h1 className="movie-title">{movieDetails.title}</h1>
            <div className="movie-poster">
                <img src={`${IMAGE_BASEURL}${movieDetails.poster_path}`} alt={movieDetails.title} />
            </div>
            <div className="movie-details">
                <p><strong>Release Date:</strong> {movieDetails.release_date}</p>
                <p><strong>Runtime:</strong> {movieDetails.runtime} minutes</p>
                <p><strong>Language:</strong> {movieDetails.original_language}</p>
                <p><strong>Rating:</strong> {movieDetails.vote_average} ({movieDetails.vote_count} votes)</p>
            </div>
            <div className="overview">
                <h2>Overview</h2>
                <p>{movieDetails.overview}</p>
            </div>
        </div>
    );
};

export default SingleMoviePage;

